import uParse from '@/components/gaoyia-parse/parse.vue'
export default {
	components: {
		uParse
	},
	data(){
		return {
			orgUuid:"",
			detailList:[
				{
					field:"oname",
					name:"别名",
					content:""
				},
				{
					field:"phone",
					name:"电话",
					content:""
				},
				{
					field:"addr",
					name:"地址",
					content:""
				},
				{
					field:"website",
					name:"网址",
					content:""
				}
			],
			introduce:"",
			busline:"",
			onameFull:""
		}
	},
	onLoad(e){
		this.orgUuid=e.orgUuid
		this.getData()
	},
	methods:{
		getData(){
			this.$globalMethod.ds_api({
				method:"m=B0012&token=SemXlsuV0Ko2Ugrp",
				loadmsg:"医院详情",
				showLoading:false,
				data:{
					orgUuid:this.orgUuid,						
				},
				success:(res)=>{
					if(!res || res.data.data.length==0){
						return
					}
					let detailObj=res.data.data;
					this.detailList.forEach((item)=>{
						item.content=detailObj[item.field]
					})
					this.introduce=detailObj.introduce;
					this.busline=detailObj.busline;
					this.onameFull=detailObj.onameFull;
				}					
			})

		}
	}
	
}
